.spinner {
  display: inline-block;
  width: 80px;
  height: 80px;
}

.spinner:after {
  content: "";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #d87d4a;
  border-color: #d87d4a transparent #d87d4a transparent;
  animation: spinner 1.2s linear infinite;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
